body {
  --color-blue: #00acdc;
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__link {
  font-size: 0;
}

.logo__image {
  width: 40px;
  height: 40px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  cursor: pointer;
  margin-left: 20px;
  padding: 23px 0;
  font-family: Roboto, sans-serif;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--color-blue);
}

a:active, .is-active {
  color: var(--color-blue);
  align-items: end;
  margin-left: 0;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin: 0;
  position: absolute;
  bottom: 0;
}
